import BandImg from '../../../../Components/BandImg';
import styles from '../../index.module.css';
import {github, discord} from '../../../../img/outils';
import {pharo} from '../../../../img/langages';
import InfoBlock from '../../../../Components/InfoBlock';

export function Bomber() {

    return (
        <>
            <div className={styles.introduction}>
                <h2>Mise en place du projet</h2>
                <div className={styles.BlockContent}>
                    <InfoBlock
                        cliquable={false}
                        btn_title="L'objectif du projet"
                        texte={<>
                            <p>
                                Ce projet avait pour objectif de concevoir un <strong>jeu vido</strong> simple en utilisant un langage que nous ne connaissions pas : <strong>Pharo</strong> .
                                Nous avions une grande libert sur le type de jeu  raliser, tant que nous respections les contraintes de lenvironnement.
                            </p>
                            <p>
                                Pour cela, plusieurs tapes cls ont t ncessaires :
                            </p>
                            <ul>
                                <li> L<strong>apprentissage</strong> du langage Pharo et de son environnement de dveloppement.</li>
                                <li> La <strong>conception du jeu</strong> : gameplay, objets, logique de fonctionnement.</li>
                                <li> La <strong>ralisation complte</strong> du jeu avec gestion des interactions et affichage.</li>
                            </ul>
                            <p>
                                Ce projet nous a permis de dcouvrir un paradigme diffrent de programmation (Smalltalk),
                                tout en mettant en place un jeu fonctionnel avec des mcaniques simples mais efficaces.
                            </p>
                        </>
                        }
                    />
                    <div className={styles.bandImg}>
                        <BandImg title='Le contexte' liste_img={["groupe de 3", "4h / semaine", "8 semaines"]} image={false} />
                        <BandImg title='Les langages utiliss' liste_img={[pharo]} image={true} />
                        <BandImg title='Les outils utiliss' liste_img={[github, discord]} image={true} />
                    </div>
                </div>
            </div>

            <div className={styles.Realisation}>
                <h2>Les ralistions</h2>
                <div className={styles.BlockContent}>
                    <InfoBlock
                        cliquable={false}
                        btn_title="Modlisation du jeux"
                        texte={<>
                            <p>Plusieurs objets ont t modliss pour structurer le jeu :</p>
                            <ul>
                                <li> Le <strong>plateau</strong> (cases, murs, sols...)</li>
                                <li> Le <strong>joueur</strong></li>
                                <li> Les <strong>ennemis</strong></li>
                                <li> Les <strong>objets</strong> rcuprables</li>
                                <li> Les <strong>bonus</strong></li>
                            </ul>
                        </>
                        }
                    />
                    <InfoBlock
                        cliquable={false}
                        btn_title="La gnration du plateau"
                        texte={<>
                            <p>La gnration automatique du plateau comprend :</p>
                            <ul>
                                <li> Les <strong>murs</strong></li>
                                <li> Le <strong>sol</strong></li>
                                <li> Le <strong>joueur</strong></li>
                                <li> Les <strong>botes destructibles</strong></li>
                                <li> La <strong>porte de sortie</strong></li>
                            </ul>
                        </>
                        }
                    />
                </div>

                <div className={styles.BlockContent}>
                    <InfoBlock
                        cliquable={false}
                        btn_title="Les intractions"
                        texte={<>
                            <p>Le jeu est entirement jouable dans lenvironnement Pharo. Voici les principales interactions :</p>
                            <ul>
                                <li> <strong>Lancement du jeu</strong></li>
                                <li> <strong>Dplacements du joueur</strong></li>
                                <li> Possibilit de <strong>relancer ou quitter</strong> la partie</li>
                            </ul>
                        </>
                        }
                    />

                    <InfoBlock
                        cliquable={false}
                        btn_title="Les tests unitaires"
                        texte={<>
                            <p>Des tests ont t mis en place pour garantir le bon fonctionnement du jeu :</p>
                            <ul>
                                <li> Vrification des mthodes des objets</li>
                                <li> Test des cas de <strong>victoire et dfaite</strong></li>
                                <li> Validation de la <strong>gnration du plateau</strong></li>
                            </ul>
                        </>
                        }
                    />

                </div>
            </div>
        </>
    )
}

export default Bomber;