import BandImg from '../../../../Components/BandImg';
import styles from '../../index.module.css';
import {java, junit} from '../../../../img/langages';
import {gitlab, discord} from '../../../../img/outils';
import InfoBlock from '../../../../Components/InfoBlock';

export function Labyrinthe() {

    return (
        <>
            <div className={styles.introduction}>
                <h2>Mise en place du projet</h2>
                <div className={styles.BlockContent}>
                    <InfoBlock
                        cliquable={false}
                        btn_title="L'objectif du projet"
                        texte={<>
                            <p>
                                Le projet avait pour objectif de dvelopper un <strong>jeu de labyrinthe</strong>, jouable directement dans le <strong>shell Linux</strong> .
                                Pour cela, plusieurs tapes cls ont t ralises :
                            </p>
                            <ul>
                                <li> Cration dun <strong>objet Utilisateur</strong> pour grer les informations du joueur.</li>
                                <li> Mise en place des <strong>objets reprsentant les diffrentes cases</strong> du plateau.</li>
                                <li> Dveloppement de <strong>deux gnrateurs de labyrinthes</strong> alatoires.</li>
                                <li> Intgration d<strong>objets bonus et malus</strong> pour enrichir le gameplay.</li>
                                <li> Gestion des <strong>interactions avec le terminal Linux</strong> (affichage ASCII, mouvements clavier, etc.).</li>
                            </ul>
                            <p>
                                Ce projet ma permis de renforcer mes comptences en programmation oriente objet, en logique de jeu, ainsi quen interaction terminale sur un systme Unix.
                            </p>
                        </>
                        }
                    />
                    <div className={styles.bandImg}>
                        <BandImg title='Le contexte' liste_img={["groupe de 4", "2h / semaine", "12 semaines"]} image={false} />
                        <BandImg title='Les langages utiliss' liste_img={[java, junit]} image={true} />
                        <BandImg title='Les outils utiliss' liste_img={[gitlab, discord]} image={true} />
                    </div>
                </div>
            </div>

            <div className={styles.Realisation}>
                <h2>Les ralistions</h2>
                <div className={styles.BlockContent}>
                    <InfoBlock
                        cliquable={false}
                        btn_title="Gnrateur de labyrinthe"
                        texte={<>
                            <p>Nous avons dvelopp <strong>deux gnrateurs de labyrinthes parfaits</strong> :</p>
                            <ul>
                                <li>Un algorithme de <strong>parcours en profondeur</strong> (Depth-First Search)</li>
                                <li>L<strong>algorithme de Prim</strong></li>
                            </ul>
                            <p>
                                Ces mthodes garantissent la cration de labyrinthes sans cycles, assurant un seul chemin entre deux points.
                            </p>
                        </>
                        }
                    />
                    <InfoBlock
                        cliquable={false}
                        btn_title="Le joueur"
                        texte={<>
                            <p>Le joueur est dfini par plusieurs caractristiques essentielles :</p>
                            <ul>
                                <li> Son <strong>pseudo</strong></li>
                                <li> Sa <strong>liste dobjets</strong></li>
                                <li> Sa <strong>position</strong> dans le labyrinthe</li>
                            </ul>
                        </>
                        }
                    />
                </div>

                <div className={styles.BlockContent}>
                    <InfoBlock
                        cliquable={false}
                        btn_title="Les cases"
                        texte={<>
                            <p>Nous avons implment diffrents types de cases pour le plateau :</p>
                            <ul>
                                <li> Case basique</li>
                                <li> Case de dpart</li>
                                <li> Case darrive</li>
                                <li> Cases piges</li>
                            </ul>
                        </>
                        }
                    />
                    <InfoBlock
                        cliquable={false}
                        btn_title="Les bonnus"
                        texte={<>
                            <p>Nous avons mis en place diffrents objets bonus pour aider le joueur :</p>
                            <ul>
                                <li> Saut de case</li>
                                <li> Protection contre les cases piges</li>
                            </ul>
                        </>
                        }
                    />
                </div>

                <div className={styles.BlockContent}>
                    <InfoBlock
                        cliquable={false}
                        btn_title="Les malus"
                        texte={<>
                            <p>Nous avons mis en place diffrents malus prsents sur certaines cases du plateau :</p>
                            <ul>
                                <li> Recul d'une case</li>
                                <li> Partie perdue</li>
                                <li> Retour  la case de dpart</li>
                                <li> Tlportation alatoire sur le plateau</li>
                            </ul>
                        </>
                        }
                    />
                    <InfoBlock
                        cliquable={false}
                        btn_title="Les intractions"
                        texte={<>
                            <p>Nous avons mis en place plusieurs interactions avec le terminal Linux pour rendre le jeu jouable :</p>
                            <ul>
                                <li> Slection du type de gnrateur de labyrinthe</li>
                                <li> Saisie du pseudo du joueur</li>
                                <li> Dplacements sur le plateau</li>
                                <li> Utilisation dobjets</li>
                                <li> Affichage du nombre de tours effectus</li>
                            </ul>
                        </>
                        }
                    />
                </div>
            </div>
        </>
    )
}

export default Labyrinthe;